.friendList {
  padding: 0px;
  margin: 0px;
  margin-top: 30px;
}

.item {
  margin-left: auto;
  margin-right: auto;
  list-style: none;
  box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px,
    rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
  width: 380px;
  display: flex;
  align-items: center;
  margin-top: 20px;
  padding: 20px;
}

.name {
  margin: 0px;
  font-size: x-large;
  font-weight: 700;
}

.avatar {
  margin-right: 20px;
}

.online,
.offline {
  background-color: #09f909;
  border-radius: 50px;
  width: 20px;
  height: 20px;
  margin-right: 30px;
}

.offline {
  background-color: #ff0000;
}
